/**
 * SenangWebs Kiln (SWK) - UI Styles
 * Styles for all UI panels and components
 */

/* Import SenangStart Icons */
@import "~@bookklik/senangstart-icons/dist/senangstart.min.css";

/* ====================
   Container & Layout
   ==================== */

.swk-ui-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.swk-ui-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.swk-ui-wrapper > * {
  pointer-events: auto;
}

/* ====================
   Sidebars
   ==================== */

.swk-sidebar {
  position: absolute;
  top: 0;
  bottom: 40px;
  width: 320px;
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid #e2e8f0;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.swk-sidebar-left {
  left: 8px;
  top: 8px;
  border-radius: 12px;
  max-height: 646px;
}

.swk-sidebar-right {
  right: 8px;
  top: 8px;
  border-radius: 12px;
  max-height: min-content;
}

/* Hide property panel by default - shown when objects selected */
.swk-sidebar-right.swk-hidden {
  display: none;
}

/* ====================
   Tab Switching
   ==================== */

.swk-tab-header {
  display: flex;
  gap: 6px;
  background: #f8fafc;
  border-bottom: 1px solid #e2e8f0;
  padding: 6px;
}

.swk-tab-button {
  flex: 1;
  padding: 8px 12px;
  background: transparent;
  border: none;
  border-radius: 6px;
  color: #64748b;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.swk-tab-button:hover {
  background: #e2e8f0;
  color: #334155;
}

.swk-tab-button.active {
  background: #10b981;
  color: white;
}

/* Hide inactive panels in left sidebar */
.swk-sidebar-left .swk-panel {
  display: none;
}

.swk-sidebar-left .swk-panel.active {
  display: block;
}

/* ====================
   Toolbar
   ==================== */

#swk-controls-panel {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 48px;
  padding: 6px;
}

.swk-toolbar {
  position: absolute;
  left: 0px;
  right: 0px;
  background: rgba(255, 255, 255, 0.95);
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  overflow: hidden;
}

.swk-toolbar-bottom {
  bottom: 8px;
  width: max-content;
  margin: 0 auto;
}

.swk-controls-toolbar {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
  height: 36px;
}

.swk-control-group {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 4px;
}

.swk-control-label {
  font-size: 0.75rem;
  font-weight: 600;
  color: #64748b;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* ====================
   Panels
   ==================== */

.swk-panel {
  background: white;
  flex: 0 0 auto;
}

.swk-panel-header {
  padding: 12px;
  border-bottom: 1px solid #e2e8f0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #f8fafc;
  height: 40px;
}

.swk-panel-header h3 {
  margin: 0;
  font-size: 0.875rem;
  font-weight: 600;
  color: #1e293b;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.swk-panel-content {
  padding: 12px;
  max-height: 600px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.swk-panel-button {
  background: none;
  border: none;
  padding: 4px 8px;
  cursor: pointer;
  font-size: 1rem;
  color: #64748b;
  border-radius: 4px;
}

.swk-panel-button:hover {
  background: #e2e8f0;
  color: #1e293b;
}

/* ====================
   Shapes Panel
   ==================== */

.swk-shapes-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 8px;
}

.swk-shape-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 12px 8px;
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s;
}

.swk-shape-button:hover {
  background: #10b981;
  border-color: #10b981;
  color: white;
}

.swk-shape-icon {
  font-size: 2.4rem;
  margin-bottom: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
}

.swk-shape-icon svg {
  width: 100%;
  height: 100%;
}

.swk-shape-label {
  font-size: 0.75rem;
  font-weight: 500;
}

/* ====================
   Property Panel
   ==================== */

.swk-property-empty {
  text-align: center;
  padding: 40px 20px;
  color: #94a3b8;
}

.swk-property-empty p {
  margin: 0 0 8px 0;
  font-weight: 500;
  color: #64748b;
}

.swk-property-empty small {
  font-size: 0.75rem;
}

.swk-property-section {
  margin-bottom: 0px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.swk-property-section-title {
  font-size: 0.75rem;
  font-weight: 600;
  color: #64748b;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin: 0 0 0 0;
  padding-bottom: 4px;
  border-bottom: 1px solid #e2e8f0;
}

.swk-property-group {
  margin-bottom: 0px;
}

.swk-property-label {
  display: block;
  font-size: 0.75rem;
  font-weight: 500;
  color: #475569;
  margin-bottom: 4px;
}

.swk-property-input {
  width: 100%;
  padding: 6px 8px;
  border: 1px solid #e2e8f0;
  border-radius: 4px;
  font-size: 0.875rem;
  font-family: inherit;
}

.swk-property-input:focus {
  outline: none;
  border-color: #10b981;
  box-shadow: 0 0 0 3px rgba(16, 185, 129, 0.1);
}

.swk-property-input[readonly] {
  background: #f8fafc;
  color: #64748b;
}

.swk-property-vector {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 4px;
}

.swk-property-color-inputs {
  display: flex;
  gap: 4px;
}

.swk-property-color {
  height: 32px;
  width: 32px;
  padding: 0 1px;
  cursor: pointer;
}

.swk-property-color-inputs #prop-color-hex {
  width: 80px;
}

.swk-property-checkbox {
  width: auto;
  margin: 0;
}

.swk-property-note {
  font-size: 0.75rem;
  color: #64748b;
  margin: 0;
  line-height: 1.5;
}

.swk-property-range-wrapper {
  display: flex;
  align-items: center;
  gap: 8px;
}

.swk-property-input.swk-property-range-value {
  width: 32px;
  text-align: center;
  padding: 6px 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #475569;
}

/* ====================
   Outliner Panel
   ==================== */

.swk-outliner-empty {
  text-align: center;
  padding: 40px 20px;
  color: #94a3b8;
}

.swk-outliner-empty p {
  margin: 0 0 8px 0;
  font-weight: 500;
  color: #64748b;
}

.swk-outliner-empty small {
  font-size: 0.75rem;
}

.swk-outliner-tree {
  font-size: 0.875rem;
}

.swk-outliner-item {
  margin-bottom: 2px;
}

.swk-outliner-item-content {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 8px;
  cursor: pointer;
  border-radius: 4px;
  transition: background 0.15s;
}

.swk-outliner-item-content:hover {
  background: #f1f5f9;
}

.swk-outliner-item-selected .swk-outliner-item-content {
  background: #dbeafe;
  /* font-weight: 500; */
  margin-bottom: 4px;
}

.swk-outliner-toggle {
  cursor: pointer;
  user-select: none;
  font-size: 0.75rem;
  color: #64748b;
  width: 16px;
  text-align: center;
}

.swk-outliner-icon {
  font-size: 0.875rem;
  color: #64748b;
  width: 16px;
  text-align: center;
  display: flex;
  align-items: center;
}

.swk-outliner-name {
  flex: 1;
  color: #1e293b;
}

.swk-outliner-type {
  font-size: 0.75rem;
  color: #94a3b8;
  text-transform: uppercase;
}

/* ====================
   Controls Panel
   ==================== */

.swk-button-group {
  display: flex;
  gap: 0;
}

.swk-button-group > * {
  border-radius: 0;
  margin: 0;
}

.swk-button-group > *:first-child {
  border-radius: 6px 0 0 6px;
}

.swk-button-group > *:last-child {
  border-radius: 0 6px 6px 0;
}

.swk-control-button {
  padding: 8px;
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  cursor: pointer;
  font-size: 0.875rem;
  transition: all 0.15s;
  color: #475569;
  font-weight: 500;
  height: 36px;
  min-width: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.swk-control-button ss-icon {
  font-size: 1.2rem;
}

.swk-control-button:hover {
  background: #e2e8f0;
  color: #1e293b;
}

.swk-control-button:active {
  transform: scale(0.98);
}

.swk-control-button.active {
  background: #10b981;
  border-color: #10b981;
  color: white;
}

.swk-control-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.swk-control-button-danger {
  color: #ef4444;
}

.swk-control-button-danger:hover {
  background: #fef2f2;
  border-color: #fecaca;
}

.swk-control-select {
  padding: 6px 8px;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
  font-size: 0.875rem;
  background: #f8fafc;
  cursor: pointer;
  font-family: inherit;
  height: 36px;
  min-width: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.swk-control-select:focus {
  outline: none;
  border-color: #10b981;
  box-shadow: 0 0 0 3px rgba(16, 185, 129, 0.1);
}

/* ====================
   Scrollbars
   ==================== */

.swk-panel-content::-webkit-scrollbar,
.swk-sidebar::-webkit-scrollbar {
  width: 8px;
}

.swk-panel-content::-webkit-scrollbar-track,
.swk-sidebar::-webkit-scrollbar-track {
  background: #f1f5f9;
}

.swk-panel-content::-webkit-scrollbar-thumb,
.swk-sidebar::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 4px;
}

.swk-panel-content::-webkit-scrollbar-thumb:hover,
.swk-sidebar::-webkit-scrollbar-thumb:hover {
  background: #94a3b8;
}

/* ====================
   Responsive
   ==================== */

@media (max-width: 1024px) {
  .swk-sidebar {
    width: 300px;
  }

  .swk-control-label {
    display: none;
  }
}

@media (max-width: 768px) {
  .swk-sidebar {
    width: 280px;
  }

  .swk-shapes-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
